@charset "UTF-8";
body {
  font-family: "Gotu", "Noto Sans JP", sans-serif;
  color: #0D2936;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4814814815vw;
  }
}
@media (min-width: 1080px) {
  html {
    font-size: 16px;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeLegibility;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}

.button {
  display: inline-block;
  background-color: #408F95;
  width: 12.625rem;
  text-align: center;
  cursor: pointer;
  color: #fff;
  padding: 1rem 0;
  font-size: 0.875rem;
  line-height: 1.571;
  z-index: 1;
  position: relative;
  transition: color 0.2s;
}

.button::first-letter {
  text-transform: capitalize;
}

.button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  transform: translate(0.25rem, 0.25rem);
  border: 0.0625rem solid #408F95;
  z-index: -2;
}

.button span {
  padding: 2.875rem;
  position: relative;
}

.button span::after {
  content: "";
  position: absolute;
  width: 2.5rem;
  height: 0.375rem;
  top: 48%;
  right: 0;
  transform: translateY(-50%);
  background-image: url(../images/common/button-arrow1.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.campaign-card {
  box-shadow: 0.125rem 0.125rem 0.625rem rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
}

.campaign-card__img img {
  aspect-ratio: 333/223;
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.campaign-card__body {
  padding: 1rem 1rem 1.5rem;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 768px) {
  .campaign-card__body {
    padding: 1.5rem 1rem 2.5rem;
  }
}

.campaign-card__tag {
  display: inline-block;
  color: #408F95;
  border: 0.0625rem solid #408F95;
  font-size: 0.75rem;
  line-height: 1;
  padding: 0.5rem 0.75rem;
  font-family: "Noto Serif JP", sans-serif;
}

.campaign-card__title {
  padding-top: 0.25rem;
  padding-bottom: 0.5rem;
  font-size: 1rem;
  line-height: 1.5;
  font-family: "Noto Serif JP", sans-serif;
  font-weight: 500;
}

.campaign-card__wrapper {
  border-top: 1px solid #408F95;
  text-align: center;
}

.campaign-card__text {
  margin-top: 0.5rem;
  font-size: 0.875rem;
  font-family: "Noto Serif JP", sans-serif;
  line-height: 1.714;
}

.campaign-card__price {
  margin-top: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.campaign-card__price-before {
  font-size: 1.25rem;
  font-weight: bold;
  line-height: 1;
  font-family: "Lato", serif;
  position: relative;
}

.campaign-card__price-before::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 39%;
  border-top: 3px solid #C94800;
}

.campaign-card__price-after {
  padding-left: 1rem;
  font-size: 2rem;
  color: #408F95;
  line-height: 1;
  font-weight: bold;
  font-family: "Lato", serif;
}

.campaign-cards__items {
  display: grid;
  grid-auto-flow: column;
}

.campaign {
  padding: 40px 0 80px;
}
@media screen and (min-width: 768px) {
  .campaign {
    padding: 120px 0 140px;
  }
}

.campaign__inner {
  position: relative;
}

.campaign__slider {
  margin-top: 1.5rem;
}

.footer-nav {
  width: 100%;
  display: flex;
}

.footer-nav__left,
.footer-nav__right {
  width: calc(50% - 0.3125rem);
}
@media screen and (min-width: 768px) {
  .footer-nav__left,
  .footer-nav__right {
    width: calc(50% - 0.875rem);
    display: flex;
    flex-direction: column;
    height: 16.125rem;
    flex-wrap: wrap;
    row-gap: 1rem;
    column-gap: 2.8125rem;
  }
}

.footer-nav__right {
  margin-left: 0.625rem;
}
@media screen and (min-width: 768px) {
  .footer-nav__right {
    margin-left: 1.75rem;
  }
}

.footer-nav__left + .footer-nav__right {
  margin-left: 1rem;
}

.footer-nav__left-items,
.footer-nav__right-items {
  display: flex;
  flex-direction: column;
}

.footer-nav__left-item a,
.footer-nav__right-item a {
  width: 100%;
  display: block;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  padding-left: 1.375rem;
  color: #fff;
  font-weight: 400;
  line-height: 1.125;
}

.footer-nav__left-item:first-child a,
.footer-nav__right-item:first-child a {
  font-weight: 700;
  position: relative;
}

.footer-nav__left-item:first-child a::before,
.footer-nav__right-item:first-child a::before {
  content: "";
  position: absolute;
  width: 1.125rem;
  height: 1.125rem;
  top: 0.5rem;
  left: 0;
  background-image: url(../images/common/starfish.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.footer {
  background-color: #408F95;
  padding-top: 3rem;
  padding-bottom: 3.5rem;
}
@media screen and (min-width: 768px) {
  .footer {
    padding-top: 5rem;
    padding-bottom: 5rem;
  }
}

@media screen and (min-width: 768px) {
  .footer__heading {
    display: flex;
    align-items: flex-end;
  }
}

.footer__logo img {
  width: 7.5rem;
}
@media screen and (min-width: 768px) {
  .footer__logo img {
    width: 12.5rem;
  }
}

.footer__sns-links {
  margin-top: 1rem;
  display: flex;
}
@media screen and (min-width: 768px) {
  .footer__sns-links {
    margin-top: 0;
    margin-left: 2.5rem;
    padding-bottom: 0.3125rem;
  }
}

.footer__sns-link + .footer__sns-link {
  margin-left: 1rem;
}

.footer__sns-link img {
  width: 1.5rem;
  height: 1.5rem;
}
@media screen and (min-width: 768px) {
  .footer__sns-link img {
    width: 2rem;
    height: 2rem;
  }
}

.footer__nav {
  margin-top: 2.5rem;
}
@media screen and (min-width: 768px) {
  .footer__nav {
    margin-top: 1.5rem;
  }
}

.footer__copyright {
  margin-top: 2.5rem;
  display: flex;
  justify-content: center;
  font-size: 0.75rem;
  color: #fff;
}
@media screen and (min-width: 768px) {
  .footer__copyright {
    margin-top: 3.4375rem;
    font-size: 0.875rem;
  }
}

.hamburger {
  position: relative;
  cursor: pointer;
  z-index: 9999;
  width: 2rem;
  height: 1.5rem;
}

.hamburger span {
  position: absolute;
  width: inherit;
  height: 0.0625rem;
  background-color: #fff;
  transition: 0.3s;
}

.hamburger span:nth-child(1) {
  top: 15%;
}

.hamburger span:nth-child(2) {
  top: 51%;
}

.hamburger span:nth-child(3) {
  top: 85%;
  opacity: 1;
}

.hamburger.is-active span:nth-child(1) {
  top: 60%;
  transform: rotate(45deg);
}

.hamburger.is-active span:nth-child(2) {
  top: 51%;
  opacity: 0;
}

.hamburger.is-active span:nth-child(3) {
  top: 60%;
  transform: rotate(-45deg);
}

.header-layout {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
}

.header {
  background-color: rgba(13, 41, 54, 0.68);
  width: 100%;
  height: 5rem;
}
@media screen and (min-width: 768px) {
  .header {
    height: 5.625rem;
  }
}

.header__inner {
  height: inherit;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 0.9375rem;
}
@media screen and (min-width: 768px) {
  .header__inner {
    padding: 0 40px;
  }
}

.header__logo {
  width: 6.375rem;
  height: inherit;
  z-index: 9999;
}
@media screen and (min-width: 768px) {
  .header__logo {
    width: 8.3125rem;
  }
}

.header__logoLink {
  height: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header__hamburger {
  height: inherit;
}

.header__pc-nav {
  height: inherit;
}

.heading {
  text-align: center;
}

.heading__main {
  font-size: 2rem;
  line-height: 1.25;
  text-transform: capitalize;
}
@media screen and (min-width: 768px) {
  .heading__main {
    font-size: 2.5rem;
    line-height: 1;
  }
}

.heading__main::first-letter {
  color: #408F95;
}

.heading__sub {
  margin-top: 0.25rem;
  font-size: 0.75rem;
  color: #408F95;
  font-family: "Noto Serif JP", sans-serif;
  font-weight: 300;
}
@media screen and (min-width: 768px) {
  .heading__sub {
    margin-top: 0.5rem;
  }
}

.inner {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (min-width: 768px) {
  .inner {
    max-width: 1130px;
    padding-right: 25px;
    padding-left: 25px;
  }
}

.mv {
  width: 100%;
  height: 100vh;
}

.mv__inner {
  position: relative;
  width: 100%;
  height: inherit;
}

.mv__slider,
.swiper-slide picture,
.swiper-slide img {
  height: inherit;
}

.swiper-slide img {
  object-fit: cover;
}

.mv__title-wrap {
  position: absolute;
  z-index: 10;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.mv__title {
  font-size: 2.5rem;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 0.54em;
  font-weight: 300;
  font-family: "Lato", serif;
}
@media screen and (min-width: 768px) {
  .mv__title {
    font-size: 5rem;
  }
}

.mv__sub-title {
  font-size: 1.25rem;
  color: #fff;
  text-align: center;
  font-weight: 400;
}

.pc-nav__items {
  display: flex;
  align-items: center;
  justify-content: center;
  height: inherit;
}

.pc-nav__item,
.pc-nav__item a {
  height: inherit;
}

.pc-nav__item a {
  display: flex;
  height: inherit;
  align-items: center;
  justify-content: center;
  color: #fff;
  padding: 0 20px;
}

.pc-nav__item-wrap {
  position: relative;
}

.pc-nav__item-en {
  display: block;
  text-align: center;
  font-size: 1.125rem;
  line-height: 1;
}

.pc-nav__item-jp {
  display: block;
  margin-top: 0.25rem;
  font-size: 0.625rem;
  line-height: 1;
  text-align: center;
}

.pc-nav__item:first-child a {
  padding-left: 0;
}

.pc-nav__item:last-child a {
  padding-right: 0;
}

.sp-nav {
  display: none;
  position: absolute;
  z-index: 5000;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  background-color: #408F95;
}

.sp-nav.is-active {
  display: block;
}

.sp-nav__inner {
  display: flex;
  padding: 6rem 0.9375rem 5rem;
}

.sp-nav__left,
.sp-nav__right {
  width: 50%;
}

.sp-nav__left-items,
.sp-nav__right-items {
  display: flex;
  flex-direction: column;
}

.sp-nav__left-item a,
.sp-nav__right-item a {
  width: 100%;
  display: block;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  padding-left: 1.375rem;
  color: #fff;
  font-weight: normal;
  line-height: 1.125;
}

.sp-nav__left-item:first-child a,
.sp-nav__right-item:first-child a {
  font-weight: bold;
  position: relative;
}

.sp-nav__left-item:first-child a::before,
.sp-nav__right-item:first-child a::before {
  content: "";
  position: absolute;
  width: 1.125rem;
  height: 1.125rem;
  top: 0.5rem;
  left: 0;
  background-image: url(../images/common/starfish.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}